<template>
    <div class="flex items-center justify-end fixed top-0 left-0 w-full h-screen z-[-10]">
        <div class="absolute w-full h-full pt-5 z-10 bg-gray-300/75"></div>
        <img alt="ESFM-Anonymous" class="w-1/2" src="../assets/img/cube.webp" draggable="false">
    </div>
    <nav class="flex items-center text-white py-2 bg-gray-800 z-30">
        <button class="flex-1"> {{title}} </button>
        <button class="px-5" @click="back"> Regresar </button>
    </nav>

    <div class="p-5 pt-10">
        <p class="text-5xl text-gray-700 font-bold" style="font-family: 'Fuzzy Bubbles', cursive;"> {{title}} </p>
        <p class="w-2/3 mx-auto text-justify mt-4" v-html="desc"></p> 

        <div class="mt-5 space-x-4">
            <button class="btn px-8" v-for="(o,index) in op" :key="'op'+o.value" @click="vota(o.value)" :class="colors[index]"> {{o.texto}} </button>
        </div>
        <p class="mt-5"> <span class="text-red-400 font-bold text-xl">Recuerda que:</span> solo puedes emitir tu voto una sola vez </p>
        <div class="w-5/6 mx-auto my-5">
            <p class="text-justify"> {{info}} </p>
        </div>
    </div>
</template>

<script>
import query from "@/mixins/query"
export default {
    name: 'votacionView',
    data(){
        return {
            name: sessionStorage.name,
            op: [],
            desc: '',
            title: '',
            res: [],
            info: '',
            colors: ["bg-green-600","bg-red-500", "bg-blue-500", "bg-yellow-400"]
        }
    },
    methods: {
        async vota(val){
            const {state, message} = await query({url:'votar', body:{voto:val, encuesta:this.$route.params.id}});
            if (state=='Success') this.$router.push({name: 'dashboard'});
            else if (state=='Error' && message=='VR') alert('Ya has emitido tu voto');
            else alert('Algo salio mal al registrar tu voto, espera unos segundos y vuelve a intentar');
        },
        back (){
            this.$router.push({name:'dashboard'});
        }
    },
    async mounted (){
        const {data} = await query({url:'getencuesta/'+this.$route.params.id, method:'GET'});
        this.op = data.op;
        this.title = data.info.title;
        this.desc = data.info.desc;
        this.info = data.info.info;
        this.res = data.r;

        this.res.forEach( (element, i) => {
            this.desc = this.desc.replace("r{"+i+"}r", atob(this.res[i].contenido));
        });
    }
}
</script>
