<template>
    <div class="flex items-center justify-end fixed top-0 left-0 w-full h-screen z-[-10]">
        <div class="absolute w-full h-full pt-5 z-10" style="background:linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,9,121,0.5) 0%, rgba(244,122,4,0.75) 100%);"></div>
        <img alt="ESFM-Anonymous" class="w-1/2" src="../assets/img/cube.webp" draggable="false">
    </div>
    <nav class="flex items-center text-white py-2 bg-gray-800 z-30">
        <button class="flex-1"> Votaciones ESFM </button>
        <button class="px-5" @click="exit"> Salir </button>
    </nav>
    
    <div class="min-h-[80vh]">
        <div class="flex flex-1 p-5 py-10 z-20">
            <div class="flex flex-col flex-1">
                <p class="text-3xl font-bold"> Bienvenido {{name}}</p>
                <p> Recuerda que solo puedes emitir tu voto una vez en cada encuesta y este voto es totalmente anonimo. </p>
                <div class="flex w-5/6 mx-auto mt-8 space-y-4 md:space-y-0">

                    <div class="flex-1 px-2" v-for="(e,index) in encuestas" :key="'enc'+index">
                        <div class="flex flex-col mx-auto px-5 rounded-md border-2 bg-gray-800/75 text-white">
                            <p class="text-xl font-bold my-4"> {{e.title}} </p>
                            <p class="text-justify"> {{e.desc}} </p>
                            <div class="flex flex-col justify-center space-y-3 my-5 sm:flex-row sm:space-x-2 sm:space-y-0">
                                <button class="btn border-none" :class="priv? 'bg-green-700':'bg-red-400'" @click="access(index)"> Acceder a la encuesta </button>
                            </div>
                        </div>
                    </div>

                </div>
            </div>
        </div>
    </div>
    <footer class="w-full min-h-[10vh] bg-gray-800 text-white py-5">
        <div class="w-5/6 mx-auto md:w-2/3">
            <p class="text-2xl mb-5"> Aviso de privacidad </p>
            <p class="text-left mb-3"> En <b class="mx-2">&gt; ESFM_Dev</b> nos tomamos muy en serio tu privacidad y el uso que tendrán tus datos, es por ello que: </p>
            <ul class="text-left font-sm list-disc pl-4">
                <li> No almacenamos información personal que pueda comprometer tu persona. </li>
                <li> Con la intención de mantener el anonimato, no se almacena la elección de tu voto. </li>
                <li> No solicitamos ninguna contraseña o usuario de acceso de algun otro servicio. </li>
                <li> Tras terminar el movimiento de estudiantes, toda información será eliminada. </li>
                <li> Toda información almacenada se comparte únicamente con los organizadores de las encuestas. Dichas encuestas deben ser propuestas y analizadas para validar su legitimidad y el uso que tendrán los datos obtenidos. </li>
                <li> Se liberan comunicados con toda la información al respecto de la votación. Esta misma información la podrás encontrar dentro de cada una de las encuestas. </li>
                <li> Tras concluir toda encuesta, se libera un oficio con los resultados así como su checksum con la finalidad de evitar cambios en la información de los resultados. </li>
            </ul>
            <div class="my-5 text-2xl">
                <input type="checkbox" id="verif" v-model="priv" class="mr-4">
                <label for="verif"> Acepto el aviso de privacidad </label>
            </div>
            <img src="../assets/img/promp_blank.png" draggable="false" class="w-1/2 mx-auto">
        </div>
    </footer>
</template>

<script>
import query from '@/mixins/query'

export default {
  name: 'dashboardHome',
  data (){  
      return {
          encuestas: [],
          name: sessionStorage.name,
          priv: false
      }
  },
  methods: {
      access (val){
          if (!this.priv){
              alert("Acepta el aviso de privacidad en la parte inferior");
              return 0;
          }
          this.$router.push({name:'vota', params:{id:this.encuestas[val].id}})
      },
      exit(){
          sessionStorage.removeItem('name');
          sessionStorage.removeItem('token');
          this.$router.push({name:'home'});
      }
  },
  async mounted (){
      const {state, data} = await query({url:'list', method:'GET'});
      if (state=='Success') this.encuestas = data;
  },
}
</script>

